import React,{useState} from "react";
import '../CSS/NewsCardStyle.css'
import { Link } from "react-router-dom";

const NewsCard = (props) => {
    const [body, setBody] = useState('')
    function showFullStory (){
        setBody(props.blogBody)
    }

    const linkStyle = {
        display:'block',
        textDecoration: "none",
        color: 'blue',
        height:'100%',
        width:'100%',
        cursor:'pointer',
      };

    return (
        <div>
            <div className="NewsBox">
                <div className='img'>
                    <Link 
                        to={`/guthaninews/News/${props.blogId}`} state={{Body:body,Title:props.blogTitle}}
                        style={linkStyle}
                    >
                        <p>Image ll be put here...</p>
                    </Link>
                </div>
                <div className="blogTitle">
                    <Link to={`/guthaninews/News/${props.blogId}`} state={{Body:body,Title:props.blogTitle}}>
                        <p>
                        {props.blogTitle}
                        </p>
                    </Link>
                    
                </div>
                {/* <button className="btn-read-more">
                    <Link to={`/News/${props.blogId}`} state={{Body:body,Title:props.blogTitle}}>FullStory</Link>
                </button> */}
            </div>
        </div>
    );
}
export default NewsCard;
